        "Shift-Ctrl-Alt-I": "image",
        "Shift-Ctrl-L": "link",
        "Shift-Ctrl-O": "list-ol",
        "Shift-Ctrl-P": "preformatted-text",
        "Shift-Ctrl-T": "table",
        "Shift-Alt-P": "pagebreak",
        "F9": "watch",
        "F10": "preview",
        "F11": "fullscreen"
